class MusicPlayer {
  play(asset: number) { postMessage({ type: 'playMusic', asset }) }
  pause() { postMessage({ type: 'pauseMusic' }) }
  stop() { postMessage({ type: 'stopMusic' }) }
}

class SfxPlayer {
  play(asset: number) { postMessage({ type: 'playSfx', asset }) }
  playRandom(...assets: number[]) { this.play(assets[Math.floor(Math.random() * assets.length)]) }
}

export const musicPlayer = new MusicPlayer()
export const sfxPlayer = new SfxPlayer()
